<template>
  <div class="login">
    <el-form :model="form" ref="form" label-width="100px">
      <el-form-item label="用户名">
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item label="身份">
        <el-select v-model="form.identity" placeholder="请选择身份">
          <el-option label="值班员" value="librarian"></el-option>
          <el-option label="管理员" value="administrator"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit()">登录</el-button>
        <el-button type="text" @click="retrievePassword()">找回密码</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data: function() {
    return {
      form: {
        username: null,
        password: null,
        identity: null
      }
    };
  },
  methods: {
    submit() {
      this.axios({
        method: "post",
        url: "http://localhost:8084/oauth/token",
        data: {
          grant_type: "password",
          username: this.form.username,
          password: this.form.password
        },
        transformRequest: [
          data => {
            return this.qs.stringify(data);
          }
        ],
        headers: {
          'Access-Control-Allow-Origin' : '*',
          'Content-Type': 'application/x-www-form-urlencoded',
          'Authorization': 'Basic YW5kcm9pZDphbmRyb2lk'
        }
      })
      .then(response => {
        if (response.status == 200) {
          this.axios.defaults.headers.common['Authorization'] = 'Bearer ' + response.data.access_token;
          if (this.form.identity == 'librarian') {
            this.$router.push({path:'/librarian/attendance'});
          } else {
            this.$router.push({path:'/administrator/shifts'});
          }
        }
      })
      .catch(error => {
        if (error.response.data == "DisabledException") {
          this.$message.error("账号已被禁用");
        } else if (error.response.data == "BadCredentialsException") {
          this.$message.error("用户名或密码错误");
        } else {
          this.$message.error("未知错误");
        }
      });
    },
    retrievePassword() {
      this.$alert('请联系网络管理员找回密码！', '找回密码', {
        confirmButtonText: '确定'
      });
    }
  }
}
</script>
