<template>
  <div class="administrator-header">
    <el-menu :default-active="activeIndex" mode="horizontal" router>
      <el-menu-item index="shifts">班次</el-menu-item>
      <el-menu-item index="students">学生</el-menu-item>
      <el-menu-item index="export">导出</el-menu-item>
      <el-menu-item index="settings">设置</el-menu-item>
      <el-submenu style="float: right;">
        <template slot="title">{{ name }}</template>
        <el-menu-item @click="logout" style="text-align: center;">退出</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'AdministratorHeader',
  data: function() {
    return {
      activeIndex: 'shifts',
      name: null
    };
  },
  created: function() {
    this.axios({
      method: "get",
      url: "/get-name",
    })
    .then(response => {
      if (response.status == 200) {
        this.name = response.data;
      }
    });
  },
  methods: {
    logout() {
      this.axios({
        method: "get",
        url: "/logout"
      })
      .then(response => {
        if (response.status == 200) {
          this.$router.push({path:'/login'});
        }
      });
    }
  }
}
</script>
