<template>
  <div class="login">
    <el-form :model="form" ref="form" label-width="100px">
      <el-form-item label="手机">
        <el-input v-model="telephone"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="modifyTelephone()">修改手机</el-button>
      </el-form-item>
    </el-form>
    <el-form :model="form" ref="form" label-width="100px">
      <el-form-item label="旧密码">
        <el-input type="password" v-model="oldPassword"></el-input>
      </el-form-item>
      <el-form-item label="新密码">
        <el-input type="password" v-model="newPassword"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="modifyPassword()">修改密码</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'Login',
  data: function() {
    return {
      telephone: null,
      oldPassword: null,
      newPassword: null
    };
  },
  created: function() {
    this.axios({
      method: "get",
      url: "/settings/view-telephone"
    })
    .then(response => {
      if (response.status == 200) {
        if (response.data.code == 0) {
          this.telephone = response.data.data;
        } else {
          this.$message.error(response.data.message);
        }
      } else {
        this.$message.error("未知错误");
      }
    })
  },
  methods: {
    modifyTelephone() {
      this.axios({
        method: "post",
        url: "/settings/modify-telephone",
        data: {
          telephone: this.telephone
        }
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("修改成功");
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("未知错误");
        }
      })
    },
    modifyPassword() {
      this.axios({
        method: "post",
        url: "/settings/modify-password",
        data: {
          oldPassword: this.oldPassword,
          newPassword: this.newPassword
        }
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("修改成功");
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("未知错误");
        }
      })
    }
  }
}
</script>
