<template>
  <div class="add-shift">
    <el-form :model="form" ref="form" label-width="100px">
      <el-form-item label="星期">
        <el-select v-model="form.dayOfTheWeek" placeholder="请选择星期">
          <el-option v-for="item in daysOfTheWeek" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="时间范围">
        <el-time-picker format="HH:mm" is-range v-model="form.timeRange" range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间" placeholder="选择时间范围">
        </el-time-picker>
      </el-form-item>
      <el-form-item label="地点">
        <el-input v-model="form.location"></el-input>
      </el-form-item>
      <el-form-item label="时薪">
        <el-input v-model="form.hourlyPay"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit('form')">添加</el-button>
      </el-form-item>
    </el-form>
    <el-table :data="shifts" height="400" border>
      <el-table-column prop="dayOfTheWeek" label="星期" width="50"></el-table-column>
      <el-table-column prop="startTime" label="开始时间" width="100"></el-table-column>
      <el-table-column prop="endTime" label="结束时间" width="100"></el-table-column>
      <el-table-column prop="location" label="地点" width="120"></el-table-column>
      <el-table-column prop="hourlyPay" label="时薪" width="50"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'AddShift',
  data: function() {
    return {
      form: {
        dayOfTheWeek: null,
        timeRange: null,
        location: null,
        hourlyPay: null
      },
      shifts: null,
      daysOfTheWeek: [{
        value: 1,
        label: '日'
      }, {
        value: 2,
        label: '一'
      }, {
        value: 3,
        label: '二'
      }, {
        value: 4,
        label: '三'
      }, {
        value: 5,
        label: '四'
      }, {
        value: 6,
        label: '五'
      }, {
        value: 7,
        label: '六'
      }]
    };
  },
  created: function() {
    this.getShifts();
  },
  methods: {
    submit() {
      this.axios({
        method: "post",
        url: "/administrator/add-shift",
        data: {
          dayOfTheWeek: this.form.dayOfTheWeek,
          location: this.form.location,
          startTime: this.form.timeRange[0].toTimeString().substring(0, 5),
          endTime: this.form.timeRange[1].toTimeString().substring(0, 5),
          hourlyPay: this.form.hourlyPay
        }
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("添加成功");
            this.getShifts();
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("未知错误");
        }
      });
    },
    getShifts() {
      this.axios({
        method: "get",
        url: "/administrator/view-shifts"
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.shifts = response.data.data;
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("未知错误");
        }
      });
    }
  }
}
</script>
