<template>
  <div class="attendance">
    <h4>固定班次</h4>
    <el-table :data="shifts" border>
      <el-table-column prop="shift.dayOfTheWeek" label="星期" width="50"></el-table-column>
      <el-table-column prop="shift.startTime" label="开始时间" width="100"></el-table-column>
      <el-table-column prop="shift.endTime" label="结束时间" width="100"></el-table-column>
      <el-table-column prop="shift.location" label="地点" width="120"></el-table-column>
      <el-table-column prop="shift.hourlyPay" label="时薪" width="50"></el-table-column>
      <el-table-column prop="startDate" label="开始日期" width="100"></el-table-column>
      <el-table-column prop="endDate" label="结束日期" width="100"></el-table-column>
    </el-table>
    <el-button type="primary" icon="el-icon-edit-outline" @click="signIn()">签到</el-button>
    <el-button type="primary" icon="el-icon-document-checked" @click="signOut()">签退</el-button>
  </div>
</template>

<script>
var dayOfTheWeek = ['日', '一', '二', '三', '四', '五', '六']
export default {
  name: 'Attendance',
  data: function() {
    return {
      shifts: null,
    }
  },
  created: function() {
    this.axios({
      method: "get",
      url: "/librarian/view-own-regular-shifts",
    })
    .then(response => {
      if (response.status == 200) {
        if (response.data.code == 0) {
          this.shifts = response.data.data;
          for (var i = 0; i < this.shifts.length; i++) {
            this.shifts[i].shift.dayOfTheWeek = dayOfTheWeek[this.shifts[i].shift.dayOfTheWeek - 1]
          }
        } else {
          this.$message.error(response.data.message);
        }
      } else {
        this.$message.error("未知错误");
      }
    });
  },
  methods: {
    signIn() {
      this.axios({
        method: "get",
        url: "/librarian/sign-in",
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("签到成功");
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("未知错误");
        }
      });
    },
    signOut() {
      this.axios({
        method: "get",
        url: "/librarian/sign-out",
      })
      .then(response => {
        if (response.status == 200) {
          if (response.data.code == 0) {
            this.$message.success("签退成功");
          } else {
            this.$message.error(response.data.message);
          }
        } else {
          this.$message.error("未知错误");
        }
      });
    }
  }
}
</script>
