<template>
  <div class="librarian-header">
    <el-menu :default-active="activeIndex" mode="horizontal" router>
      <el-menu-item index="attendance">考勤</el-menu-item>
      <el-menu-item index="relay">替班</el-menu-item>
      <el-menu-item index="history">历史</el-menu-item>
      <el-menu-item index="settings">设置</el-menu-item>
      <el-submenu style="float: right;">
        <template slot="title">{{ name }}</template>
        <el-menu-item @click="logout" style="text-align: center;">退出</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
export default {
  name: 'LibrarianHeader',
  data: function() {
    return {
      activeIndex: 'attendance',
      name: null
    };
  },
  created: function() {
    this.axios({
      method: "get",
      url: "/get-name",
    })
    .then(response => {
      if (response.status == 200) {
        this.name = response.data;
      }
    });
  },
  methods: {
    logout() {
      this.axios({
        method: "get",
        url: "/logout"
      })
      .then(response => {
        if (response.status == 200) {
          this.$router.push({path:'/login'});
        }
      });
    }
  }
}
</script>
